  }
  .intro .title-text {
    background: #fff;
    height: 100vh;
    padding-top: 150px !important;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(251, 251, 251, 0.1)), color-stop(rgba(0, 0, 0, 0.5)), to(rgba(0, 0, 0, 0.5)));
    background: linear-gradient(rgba(251, 251, 251, 0.1), rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
    color: rgb(245, 245, 245);
  }
  @media screen and (max-width: 768px) {
    .intro .title-text {
      font-size: 12px;
    }
    .intro h1 {
      font-size: 20px;
    }
  }
  .typewriter {
    color: #fff;
    font-family: monospace;
    overflow: hidden; /* Ensures the content is not revealed until the animation */
    border-right: 0.15em solid orange; /* The typwriter cursor */
    white-space: nowrap; /* Keeps the content on a single line */
    margin: 0 auto; /* Gives that scrolling effect as the typing happens */
    letter-spacing: 0.15em; /* Adjust as needed */
    -webkit-animation: typing 3.5s steps(10, end), blink-caret 0.5s step-end;
            animation: typing 3.5s steps(10, end), blink-caret 0.5s step-end;
  }
  
  /* The typing effect */
  @-webkit-keyframes typing {
    from {
      width: 50%;
    }
    to {
      width: 60%;
    }
  }
  @keyframes typing {
    from {
      width: 50%;
    }
    to {
      width: 60%;
    }
  }
  
  /* The typewriter cursor effect */
  @-webkit-keyframes blink-caret {
    from,
    to {
      border-color: transparent;
    }
    50% {
      border-color: orange;
    }
  }
  @keyframes blink-caret {
    from,
    to {
      border-color: transparent;
    }
    50% {
      border-color: orange;
    }
  }
  
  .fa-arrow-circle-down {
    bottom: 0;
    color: red;
  }
  .rank-label-container {
    margin-top: -19px;
    text-align: center;
  }
  
  .rank-label {
    padding: 5px 10px 5px 10px;
    border-radius: 7px;
  }
  .about-info {
    padding: 4%;
  }
  
#loading-spinner {
    /* display: inline; */
    position: relative;
    z-index: 999999;
    height: 100vh !important;
  }
  
  #loading-spinner .spin-icon {
    margin: auto;
    width: 80px;
    height: 80px;
  
    border: solid 3px transparent;
    border-top-color:  #666;
    border-left-color: #666;
    border-radius: 80px;
  
    -webkit-animation: loading-spinner 500ms linear infinite;
    animation:         loading-spinner 500ms linear infinite;
    margin-top:270px;
  }
  
  @-webkit-keyframes loading-spinner {
    0%   { -webkit-transform: rotate(0deg);   transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  
  @keyframes loading-spinner {
    0%   { -webkit-transform: rotate(0deg); transform: rotate(0deg);   transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); transform: rotate(360deg); }
  }
